
<template>
  <div style="background-color: #F6F6F6;position:fixed;top:50px;bottom:0;left:0;right:0;">
    <div class="box" @click="btnD(item)" v-for="(item,i) in info" :key="i">
      <!-- 周期 -->
      <div class="round">
        <p class="di">第 {{ i+1 }} 期</p>
        <p v-if="item.payStatus==0" class="dai" style="color:#FB9E58;">待缴费</p>
        <p v-else-if="item.payStatus==1" class="dai" style="color:#02BC6C;">已缴费</p>
        <p v-else-if="newT>item.totalPric" class="dai" style="color:#FF4848;">已逾期</p>
      </div>
      <!-- 费用周期 -->
      <div class="riqi">
        <p class="riqi_l">房租周期</p>
        <p
          class="riqi_r"
        >{{ item.startTime.substr(0,10) }}&ensp;至&ensp;{{ item.endTime.substr(0,10) }}</p>
      </div>
      <!-- 付款截止日期 -->
      <div class="riqi">
        <p class="riqi_l">付款截止日期</p>
        <p class="riqi_r">{{ item.startTime.substr(0,10) }}</p>
      </div>
      <div class="totle">租金 &yen; {{ item.roomPrice }}</div>
    </div>
  </div>
</template>
<script>
import { GetBusinessRentalBillByorderroom } from '@/api/api_business_hotel_and_scale';
export default {
    data() {
        return {
            info: []
        };
    },
    created() {
        this.$store.commit('setHeader', '房费账单');
        this.id = this.$route.query.id;
        this.getinfo();
    },
    methods: {
        // 查询房费账单
        getinfo() {
            GetBusinessRentalBillByorderroom(this.id).done((res) => {
                this.info = res.data;
            });
        },
        // 跳转-房费信息
        btnD(item) {
            this.$router.push({
                path: '/roomFeeD',
                query: {
                    rentalBillId: item.id
                }
            });
        }
    },
    watch: {}
};
</script>
<style scoped>
.box {
    margin-top: 15px;
    background: #fff;
    font-size: 14px;
    line-height: 30px;
    padding: 0 10px;
}
.round {
    display: flex;
    justify-content: space-between;
}
.di {
    font-weight: bold;
}
.dai {
    color: #fb9e58;
}
.riqi {
    display: flex;
}
.riqi_l {
    color: #000;
    width: 28%;
    text-align: left;
}
.riqi_r {
    color: #b2b2b2;
}
.totle {
    color: #000;
    text-align: right;
}
</style>



